.cards {
    width: 200px;
    height: 250px;
    background: #f96d80;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin: 10px;
    color: #ffcb74;
    font-size: 0;
    transform: rotateY(180deg);
    transition: transform linear 1s;
}

